body {
  margin: 0px;
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
*, *:before, *:after {
  box-sizing: inherit;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}
a {
  color: inherit;
  text-decoration: none;
  font-style: normal;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}
p {
  margin: 0;
}
html, body {
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  position: relative;
  font-family: consolas;
  background-color: var(--background);
  transition: background-color .8s;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

input {
  border: none;
}
button {
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
  // -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
address {
  font-style: normal;
}
html {
  scroll-behavior: smooth; 
}
.slowChanges {
  animation: fade 1000ms linear 1;
  animation-delay: 200ms;
}

@keyframes fade {
  0% { opacity: 1 }
  48% { opacity: 0 }
  52% { opacity: 0 }
  100% { opacity: 1 }
}

body::-webkit-scrollbar {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 6px;
  background-color: transparent;
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: var(--yellow);
}

body::-webkit-scrollbar-track {
  box-shadow: none;
  border-radius: 10px;
  background-color: transparent;
  color: transparent;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  // min-height: 100vh;
  
  // max-width: 2200px;
  
  margin: 0 auto;
  // transition: opacity 2.1s;
  // opacity: 1;

  // &.hide {
  //   opacity: 0;
  //   transition: opacity .4s;
  // }
  
}
.container {
  position: relative;
  margin: 0 auto;
  width: 96%;
  max-width: 1200px;

  @include tablets {
    width: 92.188%;
  }
  
  @include phones {
    width: 93.75%;
  }
}
